.advantage {
  background-color: #ffffff;
}
.advantage-list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .advantage-list {
    flex-direction: row;
    justify-content: center;
  }
}
.advantage-list__item {
  box-sizing: border-box;
}
.advantage-list__card {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 52px 33px 31px 33px;
  text-align: center;
  justify-content: space-evenly;
  box-sizing: border-box;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantage-list__card {
    width: 256px;
    min-height: 256px;
  }
}
@media (min-width: 1440px) {
  .advantage-list__card {
    width: 400px;
  }
}
@media (min-width: 768px) {
  .advantage-list__item-title {
    width: 140px;
    text-align: center;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .advantage-list__item--big {
    width: 768px;
    display: flex;
  }
}
@media (min-width: 1440px) {
  .advantage-list__item--big {
    width: 1200px;
  }
}
.advantage-list__card--img {
  background-color: #81b3d2;
  color: #ffffff;
}
.advantage-list__card--icon {
  background-color: #ffffff;
  text-align: left;
  padding-left: 50px;
}
@media (min-width: 768px) {
  .advantage-list__card--icon {
