  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .feedback-form__item {
    display: flex;
    flex-direction: row;
  }
}
.feedback-form__fieldset {
  border: none;
  padding: 20px;
  margin: 0;
}
.feedback-form__legend {
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  padding: 0;
  text-align: center;
}
.feedback-form__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.feedback-form__list--check {
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .feedback-form__list--check {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .feedback-form__list--check {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .feedback-form__wide {
    grid-column: span 2;
  }
}
.feedback-form__input--check {
  display: flex;
  position: relative;
  flex-direction: row;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  padding: 10px 10px 10px 58px;
  box-sizing: border-box;
  text-transform: capitalize;
}
.feedback-form__textarea {
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  width: 100%;
  max-width: 768px;
  box-sizing: border-box;
}
@media (min-width: 1440px) {
  .feedback-form__textarea {
    display: block;
    margin: 0 auto;
  }
}
.feedback-form__button-wrapper {
  padding: 20px;
  display: grid;
}
@media (min-width: 768px) {
  .feedback-form__button-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .feedback-form__button-wrapper {
    width: 768px;
    margin: 0 auto;
  }
}
.feedback-form__button {
  display: flex;
  padding: 16px;
  background-color: #81b3d2;
  color: #ffffff;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  justify-content: center;
}
.feedback-form__footnote {
  text-align: right;
}
@media (min-width: 768px) {
  .feedback-form__footnote {
    text-align: right;
  }
}
