.site-list {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #81b3d2;
  top: 0;
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  align-items: center;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    background-color: #ffffff;
  }
}
.site-list__item {
  display: flex;
  position: relative;
  width: 100%;
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.site-list__item:last-child::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 7px;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .site-list__item {
    width: 153px;
    box-shadow: none;
  }
  .site-list__item:nth-child(2) {
    margin-right: 153px;
  }
  .site-list__item:last-child::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    width: 240px;
  }
  .site-list__item:nth-child(2) {
    margin-right: 240px;
  }
}
.site-list__link {
  display: block;
  text-decoration: none;
  color: #ffffff;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
